<template>
  <div>
    <header>
      <h1>EF-TEST</h1>
      <span class="byline">Busca las posibles opciones de palabras en un juego de letras</span>
    </header>
    <section>
      <el-row>
        <el-col :span="8" class="first-col">
          <el-input
            clearable
            placeholder="Ingrese maximo 12 letras "
            maxlength="12"
            v-model="letters"
          />
        </el-col>

        <el-col :span="3">
          <el-input
            clearable
            label="hola"
            type="number"
            placeholder="Ingrese el numero de letras que contiene la palabra"
            v-model="num"
          />
        </el-col>
        <el-col :span="3">
          <el-input placeholder="Empieza con" v-model="startWith" />
        </el-col>
        <el-col :span="1">
          <el-button label="Button" type="success" @click="findOptions()">Buscar</el-button>
        </el-col>
      </el-row>
    </section>
    <div v-loading="loading" class="results">
      <template v-if="results.length > 0">
        <h1>Resultados</h1>
        <ul>
          <li v-for="word in results" :key="word.id">{{ word.word }}</li>
        </ul>
      </template>
      <span v-else>No se encontraron resultados</span>
    </div>
    <pagination
      v-show="total > 0"
      :total="total"
      :page.sync="query.page"
      :limit.sync="query.limit"
      @pagination="findOptions"
    />
  </div>
</template>

<script>
import axios from "axios";
import Pagination from "./Pagination";
export default {
  name: "HelloWorld",
  components: { Pagination },
  data() {
    return {
      letters: "TJEUINGRTSDA",
      loading: false,
      num: 3,
      startWith: "",
      results: [],
      total: 0,
      query: {
        page: 1,
        limit: 500,
      },
    };
  },

  methods: {
    findOptions() {
      this.loading = true;
      axios({
        method: "get",
        url: `/api/search/${this.getLeters()}/${this.getNum()}/${this.getStartWith()}`,
        params: this.query,
      }).then((response) => {
        console.log({ data: response });
        this.results = response.data.data;
        this.total = response.data.total;
        this.loading = false;
      });
    },
    getStartWith() {
      return this.startWith != "" ? this.startWith : "--";
    },
    getNum() {
      const num = this.num > 2 ? this.num : 2;
      this.num = num;
      return this.num;
    },
    getLeters() {
      return this.letters !== "" ? this.letters : "-";
    },
  },
};
</script>

